import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import queryString from "query-string";
import ProductAPI from "../API/ProductAPI";
import Pagination from "./Component/Pagination";

function Users(props) {
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const [documentsUsers, setDocumentsUsers] = useState([]);

  // Cp nht documentsHotels khi hotels thay i
  useEffect(() => {
    if (users) {
      setDocumentsUsers(users);
    }
  }, [users]);

  const [pagination, setPagination] = useState({
    page: "1",
    count: "8",
    search: "",
    category: "all",
  });

  const onChangeText = (e) => {
    const value = e.target.value;

    setPagination({
      page: pagination.page,
      count: pagination.count,
      search: value,
      category: pagination.category,
    });
  };

  //Tng s trang
  const [totalPage, setTotalPage] = useState();

  //Hm ny dng  thay i state pagination.page
  //N s truyn xung Component con v nhn d liu t Component con truyn ln
  const handlerChangePage = (value) => {
    console.log("Value: ", value);

    //Sau  set li ci pagination  gi chy lm useEffect gi li API pagination
    setPagination({
      page: value,
      count: pagination.count,
      search: pagination.search,
      category: pagination.category,
    });
  };

  //Gi hm useEffect tm tng s sn phm  tnh tng s trang
  //V n ph thuc v state pagination
  useEffect(() => {
    const fetchAllData = async () => {
      try {
        const url = "http://localhost:5000/admin/users";
        const response = await fetch(url, {
          method: "GET", // Ch nh phng thc GET
          headers: {
            "Content-Type": "application/json", // t tiu  Content-Type nu cn
          },
          credentials: "include", // Bao gm cookie trong yu cu
        });

        const dataJson = await response.json(); // Cn await  nhn d liu JSON
        // Kim tra nu phn hi khng ok (status khc 2xx)
        if (!response.ok) {
          if (response.status === 401) {
            // Nu nhn c m li 401, chuyn hng n trang login
            navigate("/login"); // Thay i ng dn n trang ng nhp ca bn
          } else if (response.status === 403) {
            // Nu nhn c m li 403, chuyn hng n trang login
            navigate("/login", {
              state: { message: "This account does not have access rights" },
            }); // Thay i ng dn n trang ng nhp ca bn
          } else {
            throw new Error("Network response was not ok");
          }
        }

        console.log(dataJson);
        //Tnh tng s trang = tng s user / s lng user 1 trang
        const totalPage = Math.ceil(
          parseInt(dataJson.length) / parseInt(pagination.count)
        );
        console.log(totalPage);
        setTotalPage(totalPage);
      } catch (error) {
        console.error("Error fetching products:", error);
      }
    };

    fetchAllData();
  }, [pagination]);

  // Gi hm Pagination
  useEffect(() => {
    const fetchData = async () => {
      const params = {
        page: pagination.page,
        count: pagination.count,
        search: pagination.search,
        category: pagination.category,
      };

      const query = queryString.stringify(params);

      const newQuery = "?" + query;

      const response = await ProductAPI.getPaginationUser(newQuery);
      console.log(response);

      setUsers(response.users);
    };

    fetchData();
  }, [pagination]);

  const handleUpdate = (id) => {
    console.log(id);
    navigate(`/updateUser/${id}`);
  };

  const handleDelete = async (id) => {
    // Hin th thng bo xc nhn
    const confirmDelete = window.confirm(
      "Are you sure you want to delete this document?"
    );

    if (confirmDelete) {
      try {
        const response = await fetch(
          `http://localhost:5000/admin/delete-user/${id}`,
          {
            method: "DELETE",
            headers: {
              "Content-Type": "application/json",
            },
            credentials: "include", // Nu cn gi cookie
          }
        );
        const data = await response.json();
        if (response.ok) {
          // Cp nht li danh sch document bng cch loi b document va xa
          setDocumentsUsers((prevDocuments) =>
            prevDocuments.filter((doc) => doc._id !== id)
          );
        } else {
          const err = window.confirm(data.message);
        }
      } catch (error) {
        console.error(error);
      }
    }
  };

  return (
    <div className="page-wrapper">
      <div className="page-breadcrumb">
        <div className="row">
          <div className="col-7 align-self-center">
            <h4 className="page-title text-truncate text-dark font-weight-medium mb-1">
              Basic Initialisation
            </h4>
            <div className="d-flex align-items-center">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb m-0 p-0">
                  <li className="breadcrumb-item">
                    <a href="/" className="text-muted">
                      Home
                    </a>
                  </li>
                  <li
                    className="breadcrumb-item text-muted active"
                    aria-current="page"
                  >
                    Table
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </div>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title">Users</h4>
                <input
                  className="form-control w-25"
                  type="text"
                  placeholder="Enter Search!"
                  onChange={onChangeText}
                />
                <br />
                <div className="table-responsive">
                  <table className="table table-striped table-bordered no-wrap">
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th>Fullname</th>
                        <th>Email</th>
                        <th>Phone</th>
                        <th>Edit</th>
                      </tr>
                    </thead>
                    <tbody>
                      {documentsUsers &&
                        documentsUsers.map((value) => (
                          <tr key={value._id}>
                            <td>{value._id}</td>
                            <td>{value.fullName}</td>
                            <td>{value.email}</td>
                            <td>{value.phone}</td>
                            <td>
                              <a
                                style={{
                                  cursor: "pointer",
                                  color: "white",
                                }}
                                className="btn btn-success"
                                onClick={() => handleUpdate(value._id)}
                              >
                                Update
                              </a>
                              &nbsp;
                              <a
                                style={{
                                  cursor: "pointer",
                                  color: "white",
                                }}
                                className="btn btn-danger"
                                onClick={() => handleDelete(value._id)}
                              >
                                Delete
                              </a>
                            </td>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                  <Pagination
                    pagination={pagination}
                    handlerChangePage={handlerChangePage}
                    totalPage={totalPage}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <footer className="footer text-center text-muted"></footer>
    </div>
  );
}

export default Users;
