const ProductAPI = {
  getAPI: async (navigate) => {
    const url = "http://localhost:5000/admin/products";
    const response = await fetch(url, {
      method: "GET", // Ch nh phng thc GET
      headers: {
        "Content-Type": "application/json", // t tiu  Content-Type nu cn
      },
      credentials: "include", // Bao gm cookie trong yu cu
    });

    // Kim tra nu phn hi khng ok (status khc 2xx)
    if (!response.ok) {
      if (response.status === 401) {
        // Nu nhn c m li 401, chuyn hng n trang login
        navigate("/login");
      } else if (response.status === 403) {
        // Nu nhn c m li 403, chuyn hng n trang login
        navigate("/login", {
          state: { message: "This account does not have access rights" },
        });
      } else {
        throw new Error("Network response was not ok");
      }
    }

    return response.json(); // Chuyn i d liu phn hi thnh JSON
  },

  getPagination: async (query) => {
    const url = `http://localhost:5000/admin/products/pagination${query}`;
    const response = await fetch(url, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "include", // Bao gm cookie trong yu cu
    });

    if (!response.ok) {
      throw new Error("Network response was not ok");
    }

    return response.json();
  },

  getPaginationUser: async (query) => {
    const url = `http://localhost:5000/admin/users/pagination${query}`;
    const response = await fetch(url, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "include", // Bao gm cookie trong yu cu
    });

    if (!response.ok) {
      throw new Error("Network response was not ok");
    }

    return response.json();
  },
};

export default ProductAPI;
