import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import convertMoney from "../convertMoney";

function History(props) {
  const navigate = useNavigate();
  const [errorMessage, setErrorMessage] = useState(null);
  const [history, setHistory] = useState([]);

  const [load, setLoad] = useState(false);

  const [text, setText] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      try {
        const url = "http://localhost:5000/admin/historys";
        const response = await fetch(url, {
          method: "GET", // Ch nh phng thc GET
          headers: {
            "Content-Type": "application/json", // t tiu  Content-Type nu cn
          },
          credentials: "include", // Bao gm cookie trong yu cu
        });

        const dataJson = await response.json(); // Cn await  nhn d liu JSON
        // Kim tra nu phn hi khng ok (status khc 2xx)
        if (!response.ok) {
          if (response.status === 401) {
            setErrorMessage(dataJson.message);
            // Nu nhn c m li 401, chuyn hng n trang login
            navigate("/login"); // Thay i ng dn n trang ng nhp ca bn
          } else if (response.status === 403) {
            // Nu nhn c m li 403, chuyn hng n trang login
            navigate("/login", {
              state: { message: "This account does not have access rights" },
            }); // Thay i ng dn n trang ng nhp ca bn
          } else {
            throw new Error("Network response was not ok");
          }
        }

        console.log(dataJson);

        setHistory(dataJson);
      } catch (error) {
        console.error("Error fetching products:", error);
      }
    };

    fetchData(); // Gi hm fetch khi component c mount
  }, []); // Ch chy mt ln khi component c mount

  return (
    <div className="page-wrapper">
      <div className="page-breadcrumb">
        <div className="row">
          <div className="col-7 align-self-center">
            <h4 className="page-title text-truncate text-dark font-weight-medium mb-1">
              Basic Initialisation
            </h4>
            <div className="d-flex align-items-center">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb m-0 p-0">
                  <li className="breadcrumb-item">
                    <a href="/" className="text-muted">
                      History
                    </a>
                  </li>
                  <li
                    className="breadcrumb-item text-muted active"
                    aria-current="page"
                  >
                    Table
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </div>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title">History</h4>
                <br />
                <div className="table-responsive">
                  <table className="table table-striped table-bordered no-wrap">
                    <thead>
                      <tr>
                        <th>ID User</th>
                        <th>Name</th>
                        <th>Phone</th>
                        <th>Address</th>
                        <th>Total</th>
                        <th>Delivery</th>
                        <th>Status</th>
                        <th>Detail</th>
                      </tr>
                    </thead>
                    <tbody>
                      {history &&
                        history.map((value) => (
                          <tr key={value._id}>
                            <td>{value.customer.userId}</td>
                            <td>{value.customer.name}</td>
                            <td>{value.customer.phone}</td>
                            <td>{value.customer.address}</td>
                            <td>{convertMoney(value.totalPrice)} VN</td>
                            <td>
                              {value.delivery
                                ? " Vn Chuyn"
                                : "Cha Vn Chuyn"}
                            </td>
                            <td>
                              {value.status
                                ? " Thanh Ton"
                                : "Cha Thanh Ton"}
                            </td>
                            <td>
                              <a
                                style={{
                                  cursor: "pointer",
                                  color: "white",
                                }}
                                className="btn btn-success"
                              >
                                View
                              </a>
                            </td>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <footer className="footer text-center text-muted"></footer>
    </div>
  );
}

export default History;
