import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Link, useNavigate } from "react-router-dom";
import convertMoney from "../convertMoney";
import feather from "feather-icons";

Home.propTypes = {};

function Home(props) {
  const navigate = useNavigate();
  const [stats, setStats] = useState([]);
  const [earnings, setEarnings] = useState(0);
  const [ordersCount, setOrdersCount] = useState(null);
  const [usersCount, setUsersCount] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const url = "http://localhost:5000/admin";
        const response = await fetch(url, {
          method: "GET", // Ch nh phng thc GET
          headers: {
            "Content-Type": "application/json", // t tiu  Content-Type nu cn
          },
          credentials: "include", // Bao gm cookie trong yu cu
        });

        const dataJson = await response.json(); // Cn await  nhn d liu JSON
        // Kim tra nu phn hi khng ok (status khc 2xx)
        if (!response.ok) {
          if (response.status === 401) {
            // Nu nhn c m li 401, chuyn hng n trang login
            navigate("/login");
          } else if (response.status === 403) {
            // Nu nhn c m li 403, chuyn hng n trang login
            navigate("/login", {
              state: { message: "This account does not have access rights" },
            });
          } else {
            throw new Error("Network response was not ok");
          }
        }

        console.log(dataJson); // Log ra d liu phn hi t API

        setStats(dataJson.orders); // Cp nht state vi danh sch sn phm
        setOrdersCount(dataJson.ordersCount);
        setUsersCount(dataJson.usersCount);
      } catch (error) {
        console.error("Error fetching products:", error);
      }
    };

    fetchData(); // Gi hm fetch khi component c mount
  }, []); // Ch chy mt ln khi component c mount

  useEffect(() => {
    feather.replace();
  }, []);

  useEffect(() => {
    const currentMonth = new Date().getMonth(); // Thng hin ti (0-11)
    const currentYear = new Date().getFullYear(); // Nm hin ti

    const totalEarnings = stats.reduce((total, order) => {
      const orderDate = new Date(order.createdAt);

      // Kim tra nu n hng nm trong thng v nm hin ti
      if (
        orderDate.getMonth() === currentMonth &&
        orderDate.getFullYear() === currentYear
      ) {
        return total + order.totalPrice; // Cng tng gi tr n hng vo tng
      }
      return total;
    }, 0);

    setEarnings(totalEarnings); // Cp nht trng thi doanh thu
  }, [stats]); // Chy khi orders thay i
  return (
    <div className="page-wrapper">
      <div className="page-breadcrumb">
        <div className="row">
          <div className="col-7 align-self-center">
            <div className="d-flex align-items-center">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb m-0 p-0">
                  <li className="breadcrumb-item">
                    <Link to="/">Dashboard</Link>
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </div>
      <div className="container-fluid">
        <div className="card-group">
          <div className="card border-right">
            <div className="card-body">
              <div className="d-flex d-lg-flex d-md-block align-items-center">
                <div>
                  <div className="d-inline-flex align-items-center">
                    <h2 className="text-dark mb-1 font-weight-medium">
                      {usersCount}
                    </h2>
                  </div>
                  <h6 className="text-muted font-weight-normal mb-0 w-100 text-truncate">
                    Clients
                  </h6>
                </div>
                <div className="ml-auto mt-md-3 mt-lg-0">
                  <span className="opacity-7 text-muted">
                    <i data-feather="user-plus"></i>
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div className="card border-right">
            <div className="card-body">
              <div className="d-flex d-lg-flex d-md-block align-items-center">
                <div>
                  <h2 className="text-dark mb-1 w-100 text-truncate font-weight-medium">
                    {convertMoney(earnings)}
                    <sup className="set-doller"> VN</sup>
                  </h2>
                  <h6 className="text-muted font-weight-normal mb-0 w-100 text-truncate">
                    Earnings of Month
                  </h6>
                </div>
                <div className="ml-auto mt-md-3 mt-lg-0">
                  <span className="opacity-7 text-muted">
                    <i data-feather="dollar-sign"></i>
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div className="card border-right">
            <div className="card-body">
              <div className="d-flex d-lg-flex d-md-block align-items-center">
                <div>
                  <div className="d-inline-flex align-items-center">
                    <h2 className="text-dark mb-1 font-weight-medium">
                      {ordersCount}
                    </h2>
                  </div>
                  <h6 className="text-muted font-weight-normal mb-0 w-100 text-truncate">
                    New Order
                  </h6>
                </div>
                <div className="ml-auto mt-md-3 mt-lg-0">
                  <span className="opacity-7 text-muted">
                    <i data-feather="file-plus"></i>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title">History</h4>
                <br />
                <div className="table-responsive">
                  <table className="table table-striped table-bordered no-wrap">
                    <thead>
                      <tr>
                        <th>ID User</th>
                        <th>Name</th>
                        <th>Phone</th>
                        <th>Address</th>
                        <th>Total</th>
                        <th>Delivery</th>
                        <th>Status</th>
                        <th>Detail</th>
                      </tr>
                    </thead>
                    <tbody>
                      {stats &&
                        stats.map((value) => (
                          <tr key={value._id}>
                            <td>{value.customer.userId}</td>
                            <td>{value.customer.name}</td>
                            <td>{value.customer.phone}</td>
                            <td>{value.customer.address}</td>
                            <td>{convertMoney(value.totalPrice)} VN</td>
                            <td>
                              {value.delivery
                                ? " Vn Chuyn"
                                : "Cha Vn Chuyn"}
                            </td>
                            <td>
                              {value.status
                                ? " Thanh Ton"
                                : "Cha Thanh Ton"}
                            </td>
                            <td>
                              <a
                                style={{
                                  cursor: "pointer",
                                  color: "white",
                                }}
                                className="btn btn-success"
                              >
                                View
                              </a>
                            </td>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <footer className="footer text-center text-muted"></footer>
    </div>
  );
}

export default Home;
